:root{
  font-size: 20px;
}


.container-fpage{
  height: 100vh;
  width: 100vw;

  position: fixed;
  top: 0;
  left: 0;

  background-color: #000000;

  background-image: url('back1.png');
  background-size: auto 100%;
  background-position: right;
  background-repeat: no-repeat;
}

.fpage{
  margin: 15vw 16vh 0;
  display: flex;
  flex-direction: row;
  column-gap: 3vw;
  flex-wrap: wrap;
}

.name-big{
  font-family: 'JetBrains Mono';
  font-weight: 800;
  font-size: 6rem;
  color: #F2F2F2;
  min-width: 22rem;
}

