.container-fpage{
  height: 100vh;
  width: 100vw;

  position: fixed;
  top: 0;
  left: 0;

  background-color: #000000;

  background-image: url(/static/media/back1.73f24435.png);
  background-size: auto 100%;
  background-position: right;
  background-repeat: no-repeat;
}

.fpage{
  margin: 15vw 16vh 0;
  display: flex;
  flex-direction: row;
  grid-column-gap: 3vw;
  -webkit-column-gap: 3vw;
          column-gap: 3vw;
  flex-wrap: wrap;
}

